<template>
    <div vu-dialog class="vu-dialog" @click="cancel" v-show="showDialog">
        <slot name="wrap">
            <div class="wrap" @click="wrapClick">
                <!--  -->
                <slot name="title">
                    <div class="title">{{title}}</div>
                </slot>

                <!--  -->
                <slot name="content">
                    <div class="content">{{content}}</div>
                </slot>

                <!--  -->
                <slot name="button">
                    <div class="buttons">
                        <!--  -->
                        <div class="btn" @click="submit">{{submitBtnText}}</div>
                        <!--  -->
                        <div class="btn btn-1" v-if="showCancelBtn" @click="cancel">{{cancelBtnText}}</div>
                    </div>
                </slot>
            </div>
        </slot>
    </div>
</template>

<script>
import {extend} from 'utils_path/index';

export default {
    props: {
        value: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            default: ''
        },
        content: {
            type: String,
            default: ''
        },
        showCancelBtn: {
            type: Boolean,
            default: true
        },
        submitBtnText: {
            type: String,
            default: ''
        },
        cancelBtnText: {
            type: String,
            default: ''
        }
    },

    data(){
        return {
            showDialog: this.value
        }
    },

    watch: {
        showDialog(newValue){
            this.$emit('input', newValue);
        },
        value(newValue){
            this.showDialog = this.value;
        }
    },

    methods: {
        show(options){
            this.showDialog = true;
            extend(this, options);
        },

        wrapClick(e){
            e.stopPropagation();
        },

        hide(){
            this.showDialog = false;
        },

        cancel(){
            this.hide();
            this.$emit('cancel');
        },

        submit(){
            this.hide();
            this.$emit('submit');
        }
    }
}
</script>

